.cup_all_QsQ {
    position: absolute;
    padding-right: 0;
    padding-left: 0;
    height: 100%;
    width: 50%;
    top: 10%;
    left: 65%;
    transition: trsnsform 300ms, opacity 200ms;
    will-change: transform;
    transform: rotate(15deg) scale(1.5);
    opacity: 0;
}

