.header {
    position: absolute;
    top: 0;
    left: 25%;
    margin: auto 0;
    width: 50%;
    /*height: 200px;*/
    z-index: 9;
    transform: translate3d(0, 0, 0);
}

.title {
    font-size: 3rem;
    text-align: center;
    line-height: 1em;
    color: #ffffff;
    font-family: 'Intro Book Regular', sans-serif;
    font-weight: bold;
}

.decrypt {
    font-size: 1.1rem;
    text-align: center;
    line-height: 1.1em;
    color: #E7EAED;
    opacity: .2;
    font-family: 'Intro-Light', monospace;
    font-weight: 400;
    font-style: oblique;
}

@media all and (max-width:1440px) {
    .header {
        left: 15%;
        width: 70%;
    }
}


@media all and (max-width:1280px) {
    .header {
        position: absolute;
        top: 12%;
        left: 9%;
        margin: auto 0;
        width: 85%;
        height: 200px;
        z-index: 9;
    }
    .title {
        font-size: 2rem;
    }
    .decrypt {
        font-size: 1vw;
        margin: 5px;
    }
}
@media all and (max-width:1024px) {
    .header {
        position: absolute;
        top: 12%;
        left: 9%;
        margin: auto 0;
        width: 85%;
        height: 200px;
        z-index: 9;
    }
    .title {
        text-align: center;
    }
    .decrypt {
        text-align: center;
        font-size: 2vw;

    }
}

@media all and (max-width:768px) {
    .header {
        top: 10%;
        height: auto;
        width: 75%;
        left: 13%;
    }
    .title {
        font-size: 1.5rem;
    }
    .decrypt {
        font-size: 2vw;
        margin: 5px;
    }
}
@media all and (max-width:658px) {
    .header {
        top: 10%;
        height: auto;
        width: 75%;
        left: 13%;
    }
    .title {
        font-size: 1.1rem;
    }
    .decrypt {
        font-size: 2vw;
        margin: 5px;
    }
}
@media all and (max-width:420px) {
    .header {
        top: 10%;
        height: auto;
        width: 75%;
        left: 13%;


    }
    .title {
        font-size: 1.1rem;
    }
    .decrypt {
        font-size: 2vw;
        margin: 5px;
    }
}

@media all and (max-width:376px) {
    .header {
        top: 20%;
        height: auto;
    }
    .title {
        font-size: 1.3rem;

    }
    .title > h3 {
        margin: 4px;
    }
    .decrypt {
        font-size: .7em;
        text-align: center;
        line-height: 1.1em;
        margin: 0;
        height: auto;
    }
}


@media all and (max-width:360px) {
    .header {
        top: 10%;
        height: auto;
    }
    .title {
        font-size: 1.3rem;

    }
    .title > h3 {
        margin: 4px;
    }
    .decrypt {
        text-align: center;
        line-height: 1.1em;
        font-size: 2vw;
        margin: 3px;
    }
}