.header {
    position: absolute;
    top: 35%;
    left: 18%;
    margin: 0;
    width: 45%;
    height: 400px;
    z-index: 9;
}

.title {
    font-size: 3rem;
    text-align: left;
    line-height: 1em;
    color: #ffffff;
    font-family: 'Intro-Bold', monospace;
    font-weight: 700;
}

.decrypt{
    font-size: 1.1rem;
    text-align: left;
    line-height: 1.1em;
    color: #E7EAED;
    opacity: .2;
    font-family: 'Intro-Light', monospace;
    font-weight: 300;
    font-style: oblique;
}

@media all and (max-width:1280px) {
    .header {
        position: absolute;
        top: 40%;
        left: 19%;
        margin: 0;
        width: 40%;
    }
    .title {
        font-size: 2rem;
    }
    .decrypt {
        line-height: 1.5rem;
        font-size: 1vw;
        margin: 0;
        font-family: 'Intro-Light', monospace;
    }
}
@media all and (max-width:1024px) {
    .header {
        position: absolute;
        top: 17%;
        left: 10%;
        margin: 0;
        width: 80%;
        height: 400px;
        z-index: 9;
    }
    .title {
        text-align: center;
    }
    .decrypt {
        text-align: center;
        font-size: 2vw;
    }
}

@media all and (max-width:768px) {
    .header {
        top: 10%;
        height: auto;
    }
    .title {
        font-size: 2rem;
    }
    .decrypt {
        line-height: 1.5rem;
        font-size: 2vw;
        margin: 0;
    }
}
@media all and (max-width:658px) {
    .header {
        top: 10%;
        height: auto;
    }
    .title {
        font-size: 2rem;
    }
    .decrypt {
        line-height: 1.1em;
        font-size: 3vw;
        margin: 0;
    }
}

@media all and (max-width:420px) {
    .header {
        top: 10%;
        height: auto;
        width: 75%;

    }
    .title {
        font-size: 1.1rem;
    }
    .decrypt {
        font-size: 2vw;
        margin: 5px;
    }
}

@media all and (max-width:376px) {
    .header {
        top: 20%;
        height: auto;
    }
    .title {
        font-size: 1.1rem;
    }
    .decrypt {
        font-size: .7em;
        line-height: .8em;

        margin: 5px;
    }
}



@media all and (max-width:360px) {
    .header {
        top: 10%;
        height: auto;
    }
    .title {
        font-size: 1.1rem;
    }
    .decrypt {
        line-height: .8em;
        font-size: 2vw;
        margin: 3px;
    }
}