/*@import url('https://fonts.cdnfonts.com/css/frutiger');*/


@font-face {
  font-family: "Intro-Bold";
  src: url("../src/fonts/@font-face/00884e3dc954fcc592c35ed58e578e11.eot") rel="preload";
  /* IE9*/
  src: url("../src/fonts/@font-face/00884e3dc954fcc592c35ed58e578e11.eot?#iefix") rel="preload" format("embedded-opentype"),
    /* IE6-IE8 */
    url("../src/fonts/intro/Intro-Bold.woff") format("woff"),
    /* chrome、firefox */
    url("../src/fonts/intro/Intro-Light.woff") format("woff"),
    /* chrome、firefox */
    url("../src/fonts/@font-face/00884e3dc954fcc592c35ed58e578e11.ttf") format("truetype") ,
    /* chrome、firefox、opera、Safari, Android, iOS 4.2+*/
    url("../src/fonts/@font-face/00884e3dc954fcc592c35ed58e578e11.svg#Intro Book Regular") format("svg");
  /* iOS 4.1- */
}

* {
  outline: none;
}

body {
  font-family: "Intro-Bold", monospace;
  font-size: calc(100vw / 1920 * 10);
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}



code {
  font-family: "Intro-Light", monospace;
}