.cupApp {
    display: block;
}

.cup_first {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}
.container {
    position: relative;
    width: 100%;
    /*display: flex;*/
    height: 100%;
    /*margin: 0 auto;*/
    /*justify-content: center;*/
}


.cup_all_Qs {
    position: absolute;
    padding-right: 0;
    padding-left: 0;
    height: 100%;
    width: 50%;
    top: 0;
    left: 70%;
    transition: trsnsform 300ms, opacity 200ms;
    will-change: transform;
    transform: rotate(15deg) scale(1.5) translate3d(0, 0, 0);
    opacity: 0;
}



.canvas {
    opacity: .5;
    width: 24%;
    position: absolute;
    left: 47%;
    top: -17%;
}


.cup_all {
    /*background: #0000FF;*/

    position: relative;
    display: block;
    overflow: hidden;
    margin: 0;
    z-index: -2;
    height: 100%;
    width: 100%;
    background-blend-mode: color-burn;
    /*will-change: transform;*/
    /*transition: trsnsform 5000ms, opacity 5000ms;*/

    /*opacity: 0;*/
}




.cupBox {
    position: absolute;
    /*width: 100%;*/
    /*height: auto;*/
    top: 20%;
    left: 30%;
    display: flex;
    justify-content: center;
}

