.header {
    position: absolute;
    top: 25%;
    left: 20%;
    margin: auto 0;
    width: 40%;
    /*height: 200px;*/
    z-index: 9;
    transform: translate3d(0, 0, 0);

}

.title {
    font-size: 3rem;
    text-align: left;
    line-height: 1em;
    color: #ffffff;
    font-family: 'Intro Book Regular', sans-serif;
    font-weight: bold;
}

.decrypt {
    font-size: 1.1rem;
    text-align: left;
    line-height: 1.1em;
    color: #E7EAED;
    opacity: .2;
    font-family: 'Intro-Light', monospace;
    /*font-weight: 400;*/
    font-style: oblique;
}

@media all and (max-width: 1280px) {
    .header {
        position: absolute;
        top: 10%;
        left: 6%;
        margin: auto 0;
        width: 90%;
        height: 200px;
        z-index: 9;
    }
    .title {
        text-align: center;
    }
    .decrypt {
        text-align: center;
    }
}
@media all and (max-width: 1280px) {
    .header {
