.canvas {
    opacity: 1;
    object-fit: cover;
    width: 30%;
    left: 31%;
    top: 90px;
    margin: 0 auto;
    position: absolute;
}


.cup_all_all {
    position: absolute;
    padding-right: 0;
    padding-left: 0;
    height: 100%;
    width: 50%;
    top: 35%;
    left: 29%;
    transition: trsnsform 300ms, opacity 200ms;
    will-change: transform;
    /* transform: rotate(15deg) scale(1.1); */
    opacity: 0;
}


.cup_all {
    /*background: #0000FF;*/

    position: relative;
    display: block;
    overflow: visible;
    margin: 0;
    z-index: -2;
    height: 100%;
    width: 100%;
    background-blend-mode: color-burn;
    will-change: transform;
    transition: trsnsform 5000ms, opacity 5000ms;

    /*opacity: 0;*/
}




.cupBox {
    position: absolute;
    width: 100%;
    top: 0;
    left: 30%;
    display: flex;
    justify-content: center;
}
.cupBox_2 {
    position: absolute;
    width: 100%;
    top: 0;
    left: 30%;
    display: flex;
    justify-content: center;
}
