.header {
    position: absolute;
    top: 50%;
    left: 25%;
    margin: auto 0;
    width: 40%;
    height: 200px;
    z-index: 9;
}

.title {
    font-size: 3rem;
    text-align: left;
    line-height: 1em;
    color: #ffffff;
    font-family: 'Intro Book Regular', sans-serif;
    font-weight: bold;
}

.decrypt {
    font-size: 1.1rem;
    text-align: left;
    line-height: 1.1em;
    color: #E7EAED;
    opacity: 0;
    font-family: 'Intro-Light', monospace;
    /*font-weight: 400;*/
    font-style: oblique;
}



@media all and (max-width:1280px) {
    .header {
        position: absolute;
        top: 60%;
        left: 20%;
        margin: auto 0;
        width: 25%;
        height: 200px;
        z-index: 9;
    }
    .title {
        font-size: 2rem;
    }
    .decrypt {
        font-size: 1vw;
        margin: 5px;
    }
}
@media all and (max-width:1024px) {
    .header {
        position: absolute;
        top: 18%;
        left: 32%;
        margin: auto 0;
        width: 50%;
        height: 200px;
        z-index: 9;
    }
    .decrypt {
        font-size: 2vw;
    }
}
@media all and (max-width:768px) {
