@media all and (max-width:376px) {
    .header {
        position: absolute;
        top: 20%;
        left: 0;
        margin: auto 0;
        width: 100%;
        height: auto;
        z-index: 9;
    }
    .title {
        font-size: 1.3rem;

    }
    .title > h3 {
        margin: 4px;
    }
    .decrypt {
